class IndexPage extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      collections: [],
      aboutIsOpen: false
    }

    this.counter = 0;

    this.infiniteScroll = React.createRef();

    this.allCollections = [];

    this.handleScroll = this.handleScroll.bind(this);

  }

  componentWillMount(){

    let collectionFetchData = function(data) {
      this.projectName = data.title.text;
      this.projectYear = data.year.text;
      this.projectTechnique = data.technique.text;
      this.images = data.image_gallery;
    }

    this.allCollections = [];

    this.props.data.allPrismicImagegallery.edges.forEach((item) => {
      
      this.allCollections.push(new collectionFetchData(item.node.data));

    });

    this.setState({
      collections: this.allCollections
    });


  }

  componentDidMount() {

    this.container = this.infiniteScroll;

    this.mouse = document.querySelector('.mouse');

    document.querySelector('.main-background').addEventListener('mouseenter', ()=>{
      TweenLite.to(this.mouse,0.25,{fill: 'black'})
    });

    // document.querySelector('.sc-hSdWYo').addEventListener('mouseenter', ()=>{
    //   TweenLite.to(this.mouse,0.25,{fill: 'black'})
    // });

    document.addEventListener('mousemove', this.mousePosition);

    setTimeout(() => {
      TweenLite.to(document.querySelector('.spacer-top'),1,{height: '0', 
      onComplete: () => {
        document.querySelector('.footer').style.visibility = "visible"
      }
    });
    },1500);

  }

  mousePosition = (e) => {
    const x = e.clientX;
    const y = e.clientY;
    const mouse = document.querySelector('.mouse');

    TweenLite.to(mouse, 0.2, {x: x, y: y});
  }

  handleScroll = () => {

    this.handleThrottledScroll();

}

handleAboutPageClick = () => {

  this.setState(prevState => ({
    aboutIsOpen: !prevState.aboutIsOpen
  }))

}


  render(){

    return(
      <Transition>
          <Mouse className='mouse'>
            <div className='mouse-inner'>
            <svg id="Ebene_2" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 170.1 170.1"><path class="st0" d="M145.3,27.1C129.9,11.6,108.6,2,85,2c-23.5,0-44.8,9.6-60.2,25"/></svg>

            <svg id="Ebene_1" data-name="Ebene 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 170 170.1"><title>Drag_Buttontest</title><path className="cls-1" d="M85,170.1A85.05,85.05,0,1,0,0,85a85,85,0,0,0,85,85.1"/><polyline className="cls-2" points="100.3 127.8 142.1 85 100.3 42.2"/><polyline className="cls-2" points="69.7 127.8 28 85 69.7 42.2"/></svg>
            </div>
          </Mouse>
        <About onClick={this.handleAboutPageClick}>
          <img src={PlusButton}/>
        </About>

        <StyledAboutMinus pose={this.state.aboutIsOpen ? 'openAbout' : 'closedAbout'} onClick={this.handleAboutPageClick}>
          <img src={MinusButton}/>
      </StyledAboutMinus>

      <SpacerTop className="spacer-top"/>
      <ContainerTop className='main-background'>    
      <BackgroundImage src={Logo}/>
      <Footer className={'footer'}>
        <span>©Anna Genger / website by Studio Karl Anders</span>
        <Socials>
        <span>instagram</span>
        <span>facebook</span>
        </Socials>
      </Footer>
      </ContainerTop>
      <Drawer style={{overflow: 'hidden'}} pose={this.state.aboutIsOpen ? 'openIndex' : 'closedIndex'}>  
      <SEO title="Anna Genger" keywords={[`Anna`, `Genger`, `Projects`]} />
      <CornerSnipTop/>
      <Container>
      <Controller>
      <Scene triggerHook={'onCenter'} duration={300} pin>    
      <BackgroundImage2 src={Logo}/>
      </Scene>
      </Controller>
      <ProjectDiv className={'infinite-scroll'} ref={this.infiniteScroll} onScroll={this.handleScroll}>
        {
          this.state.collections.map((item, index) => {
            return (
            <InfiniteSlider key={index} backgroundColor='white' number={index} data={item}/>
              )
          })
        }
      </ProjectDiv>
      </Container>
      <CornerSnipBottom/>
      </Drawer>
      <Spacer/>
      <StyledOverlay onClick={this.handleAboutPageClick} pose={this.state.aboutIsOpen ? 'open' : 'closed'}/>
      <AboutPage open={this.state.aboutIsOpen}/>
      </Transition>
    )
  }
}

export default IndexPage

