export const pageQuery = graphql`
  query ImageGalleries {
    allPrismicImagegallery {
      edges {
        node {
          id
          uid
          data {
            title {
              text
            }
            year {
              text
            }
            technique {
              text
            }
            image_gallery {
              image_caption {
                text
              }
              image {
                alt
                url
                localFile {
                  childImageSharp {
                    fluid(maxWidth: 1000) {
                      ...GatsbyImageSharpFluid_withWebp
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
`

